<template>
  <modal
    v-if="$modal"
    class="modal"
    :class="{
      'modal_w-close-button': closeButton,
      'modal_w-footer': hasFooter
    }"
    :name="name"
    :pivotY="0.1"
    :width="width"
    :height="height"
    adaptive
    overlayTransition=""
    @opened="isVisible = true"
    @closed="isVisible = false"
  >
    <div class="modal__body" ref="modalBody">
        <button v-if="closeButton" class="modal__close-button" @click="$modal.hide(name)">
          <img src="@/assets/icon-x.svg" />
        </button>

        <slot />
    </div>
    <footer v-if="hasFooter" class="modal__footer">
      <slot name="footer" />
    </footer>
  </modal>
</template>

<script>
import * as bodyScroll from 'tua-body-scroll-lock';

export default {
  props: {
    name: String,
    width: Number,
    height: {},
    closeButton: Boolean,
  },
  data() {
    return {
      isVisible: false,
    };
  },
  computed: {
    hasFooter() {
      return Boolean(this.$slots.footer);
    },
  },
  watch: {
    isVisible() {
      if (this.isVisible) {
        bodyScroll.lock(this.$refs.modalBody);
      } else {
        bodyScroll.unlock(this.$refs.modalBody);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/variables.scss";

.modal {
  &::v-deep {
    .vm--modal {
      background-color: var(--bg);
      border: 2px solid var(--lighter-primary);
    }

    .vm--overlay {
      background: rgba(0, 0, 0, var(--modal-overlay-opacity));
    }
  }

  &_w-footer {
    .modal__body {
      padding-bottom: 10px;
    }
  }

  &_w-close-button {
    .modal__body {
      padding-top: 45px;
    }
  }

  &__close-button {
    filter: var(--img-filter-inverted);
    position: absolute;
    top: 10px;
    right: 13px;

    img {
      width: 25px;
    }
  }

  &__body {
    position: relative;
    padding: 15px;
    overflow: auto;
    height: 100%;
  }

  &__footer {
    display: flex;
